// 20161025 TR c1 start style header bottom part


// Main Header styling
header {
  .header {
    top: 0;
    left: 0;
    padding: 0;
    display: block;
    position: relative;
    background: #37474f;
    height: 100%;
    z-index: 99;

    // Header top bar
    .top {
      display: block;
      position: relative;
      height: 150px;

      .logo-brand {
        position: absolute;
        vertical-align: middle;
        top: 37%;
        left: 3%;

        .logo-txt {
          font-size: 35px;
          font-weight: 600;
          font-family: Orbitron;
          color: #fff; } } }

    // Header bottom bar
    .bottom {
      display: block;
      position: relative;

      .menu-bar {
        display: block;
        position: relative;
        height: 100px;
        width: 100%;
        background: #2f66c1;

        .home {
          display: inline-block;
          position: relative;
          width: 115px;
          height: 100%;
          padding-top: 20px; }

        .menu-tabs {
          display: inline-block;
          position: relative;
          width: calc(83% - 115px);
          height: 100%;

          .top-menus {
            color: #fff;
            font-family: Oswald;
            font-size: 18px;
            width: 100%;
            height: 50%;
            display: block;
            padding: 20px 20px 0 20px;
            li {
              padding-right: 16px;
              padding-left: 16px; }
            &:nth-child(1) {
              padding-left: 0; }

            li > a {
              color: #fff;
              &:hover {
                color: #001d8c; } } }

          .bottom-menus {
            display: block;
            width: 100%;
            height: 50%;
            font-size: 18px;
            color: #fff;

            li {
              padding-right: 16px;
              padding-left: 16px;
              line-height: 50px;

              button {
                display: block;
                background: #2f66c1;
                border: none;
                padding: 0;
                &:hover {
                  color: #e5f327; } } } } }


        .keyword-search {
          display: inline-block;
          width: 17%;
          height: 100%;
          padding: 15px 15px 15px 0;

          .search-bar {
            border-bottom: 2px solid #fff;
            padding: 5px;

            input {
              border: none;
              background: #2f66c1;
              color: #fff;
              height: 18px;
              float: left;
              width: 90%; }

            a {
              display: inline-block;
              height: 18px;
              width: 18px;
              background: url("../i/ico-search.png"); } }


          .login-link {
            float: right;
            a {
              line-height: 50px;
              color: #fff;
              text-decoration: none;
              transition: all 0.3s;
              &:hover {
                text-decoration: underline !important; } } } } } } } }
