.btn-home {
  display: block;
  width: 54px;
  height: 54px;
  background: url("../i/icons.png");
  background-position: -32px -162px; }

// Page inner header
.inner-header {
  display: block;
  background: #36474f;
  width: 98%;
  margin-top: 256px;
  padding: 0 30px;
  margin-right: auto;
  margin-left: auto;

  .main-topic {
    font-family: 'Nixie One', cursive;
    display: inline-block;
    width: 70%;

    h1 {
      font-size: 42px;
      line-height: 88px;
      color: #fff; } } }

//  Inner page-topic
.page-topic {
  display: block;
  position: relative;
  padding-left: 3.8%;
  padding-top: 30px; }

// Inner image header
.inner-image-header {
  display: block;
  width: 100%;
  background: #9dc2ff;

  .bg-image {
    background: url("../i/contactUs/header-bar-contact.png");
    background-size: cover;
    height: 225px;
    width: 100%;
    opacity: 0.46; } }

// Filter category items
.filter-item {
  display: block;
  padding-bottom: 30px;

  .item-header {
    display: block;
    font-family: 'Slabo 27px', serif;
    padding-bottom: 15px;

    label {
      font-size: 26px;
      color: #3a3b3c;
      cursor: pointer;
      span {
        color: #3a8af7; } } }


  .item-container {
    .select-item {
      display: block;

      label {
        font-size: 20px;
        color: #3a3b3c;
        line-height: 40px; } } } }

// Buttons
.btn-submit {
  width: 45%;
  line-height: 70px;
  background: #3a81f9;
  border-radius: 10px;
  border: none;
  font-family: "Roboto", sans-serif;
  font-size: 30px;
  color: #fff;

  &:hover {
    background: #3a7be9; } }

// Slide toggle controller style
.slideable {
  display: none; }
