body,html {
  font-family: 'Raleway', sans-serif;
  a {
    text-decoration: none;
    &:hover,
    &:focus,
    &.active {
      //all: initial
      text-decoration: none !important; } } }

// Main view screen
.main-screen {
  display: block;
  position: relative;
  background: #37474f;

  // left side main content panel
  .content-panel {
    display: inline-block;
    position: relative;
    width: 83%;
    background: #fff;

    .main-slider {
      display: block;
      position: relative;
      width: 100%;

      img {
        width: 100%; } }

    .search-area {
      display: block;
      position: relative;

      .main-search-bar {
        display: block;

        .bar-holder {
          display: block;
          position: relative;
          top: -42px;

          input {
            width: 80%;
            line-height: 64px;
            border: 12px solid rgba(125,125,125,0.5);
            font-size: 24px;
            color: #918a8a;
            padding-left: 30px; } } }

      .filter-boxes {
        display: block;
        padding-bottom: 60px;

        .box-holder {
          width: 90%;

          ul > li {
            width: 29.5%;
            background: rgba(225, 225, 225, 0.6);
            padding: 8px;
            margin-top: 30px;
            margin-right: 5.5%;
            transition: all 0.3s;
            &:hover {
              background: #508ef6; }

            &:nth-child(3n) {
              margin-right: 0;
              float: right; }

            .category-box {
              background: #fff;
              display: block;
              width: 100%;
              font-family: 'Slabo 27px', serif;
              padding-bottom: 20px;

              .category-name {
                display: block;
                padding: 16px;
                a {
                  font-size: 26px;
                  color: #818181;
                  font-family: 'Slabo 27px', serif; } }

              .course-info {
                display: block;

                .image {
                  display: inline-block;
                  position: relative;
                  width: 46%;

                  img {
                    width: 90%; } }

                .description {
                  display: inline-block;
                  width: 54%;
                  position: relative;
                  font-size: 15px;
                  color: #919191;
                  line-height: 16px;
                  padding-right: 8px;
                  padding-left: 2px;

                  a {
                    padding-top: 20px;
                    color: #3a74d6;
                    font-size: 16px; } } } } } } } } }

  // right side banner panel
  .banner-panel {
    width: 17%;
    display: inline-block;
    background: #37474f;
    position: relative;
    height: 100%; } }

// inner page layout

