// All Courses page styling

.courses-screen {
  display: block;
  position: relative;

  .inner-header {
    .right-corner {
      display: inline-block;
      float: right;
      width: 30%;

      .sort-box {
        display: block;
        line-height: 88px;

        input {
          height: 40px;
          background: #4b5b66;
          width: 100%;
          border: none;
          padding-left: 18px; } } } }

  .filter-area {
    display: block;
    width: 100%;

    .category-panel {
      display: inline-block;
      position: relative;
      width: 28%;
      padding-left: 5%;
      padding-top: 38px;
      float: left;

      .all-category {
        display: block;
        padding-top: 50px; } }

    .result-panel {
      display: inline-block;
      position: relative;
      width: 72%;
      float: right;
      padding-right: 1%;

      .panel-header {
        display: block;
        width: 100%;

        .filter-path {
          display: block;
          height: 70px;
          width: 60%;

          ol {
            display: block;
            padding-top: 46px;
            font-size: 20px; } }

        .search-boxes-bar {
          display: block;
          background: #2f66c2;
          width: 100%;

          .drop-holder {
            line-height: 66px;

            input {
              height: 40px;
              background: #4065a4;
              border: 1px solid #748bb1;
              padding-left: 18px;
              width: 100%;
              color: #fff; } } } }

      .search-result-view {
        display: block;
        overflow-y: auto;

        .result-box {
          display: block;
          border-bottom: 5px solid #ededed;
          padding: 20px 10px 40px;
          &:nth-child(even) {
            background: rgba(47, 102, 194, 0.03); }


          .course-name {
            display: block;
            width: 100%;

            a {
              font-size: 25px;
              color: #3a8af7;
              font-family: "Slabo 27px", serif;
              line-height: 45px; }
            &:hover {
              text-decoration: underline; } }

          .course-info {
            display: inline-block;
            width: 72%;
            float: left;
            padding-top: 30px; }

          .provider-info {
            display: inline-block;
            float: right;
            width: 28%; } } } } } }

// Contact-Us page styling
.contact-us-screen {
  display: block;
  position: relative;

  .contact-form-holder {
    display: block;
    width: 93%;
    margin-left: auto;
    margin-right: auto;
    background: #3d5265;
    z-index: 99;
    position: relative;
    top: -60px;

    .contact-form {
      display: block;
      padding: 50px 70px 100px;

      .submit-area {
        display: inline-block;
        position: relative;
        width: 54%;
        float: left;
        padding-right: 28px;

        .submit-form {
          div > label {
            color: #cccccc;
            font-size: 22px;
            line-height: 50px;
            position: relative;

            span {
              font-size: 45px;
              color: #18a6f0;
              position: absolute;
              top: 28px;
              right: -32px;
              line-height: 20px; } }

          div > input {
            color: #dff4ff;
            line-height: 54px;
            font-size: 22px;
            border: none;
            background: #506374;
            width: 100%;
            margin-bottom: 18px;
            padding-left: 18px;
            font-family: 'Roboto', sans-serif; }

          p {
            font-size: 20px;
            color: #cccccc;
            line-height: 54px;
            position: relative;
            span {
              font-size: 45px;
              color: #18a6f0;
              position: absolute;
              top: 28px;
              left: -24px;
              line-height: 20px; } }

          .user-message {
            textarea {
              width: 100%;
              background: #506374;
              border: none;
              color: #fff;
              padding: 15px;
              line-height: 20px;
              font-weight: 200; } }

          .re-captcha {
            padding: 50px 0;
            img {
              width: 75%; } } } }

      .question-and-info-area {
        display: inline-block;
        position: relative;
        width: 46%;
        padding-left: 30px;

        .question-area {
          display: block;

          .heading {
            display: block;
            padding-bottom: 40px;
            p {
              padding-top: 10px;
              font-size: 22px;
              color: #cccccc;
              line-height: 26px; } }

          .number-of-questions {
            display: block;
            border-bottom: 3px solid #506374;
            padding-bottom: 8px;

            .question {
              display: block;
              h3 {
                font-family: Oswald;
                font-size: 24px;
                line-height: 60px;
                color: #fff;
                position: relative;
                left: 45px;
                cursor: pointer;
                span {
                  color: #18a6f0;
                  position: absolute;
                  font-size: 68px;
                  left: -45px;
                  top: -6px; } } }

            .answer {
              //display: block
              p {
                font-size: 18px;
                line-height: 20px;
                color: #6da6de;
                text-align: justify;
                padding-bottom: 10px; } } } }

        .info-area {
          display: block;
          margin-top: 120px;
          padding: 30px;
          border: 2px solid #506374;
          border-radius: 10px;

          .heading {
            display: block; }

          .address {
            display: block;
            padding-bottom: 35px;
            p {
              font-size: 24px;
              font-family: "Roboto", sans-serif;
              color: #e2e2e2;
              font-weight: lighter; } }

          .location {
            display: block;
            padding-bottom: 50px;
            iframe {
              border: 0;
              height: 235px;
              width: 100%; } }

          .contact-info {
            display: block;
            font-size: 24px;
            font-family: "Roboto", sans-serif;
            color: #e2e2e2;
            font-weight: lighter;
            .email {
              display: block;
              padding-bottom: 35px;
              color: #e2e2e2;
              text-decoration: none; }

            .tp {
              display: block;
              padding-bottom: 35px;
              line-height: 35px; }

            .fax {
              display: block; } } } } } } }

// ------------------ Main categories --------------------------------
.main-category {
  display: block;
  position: relative;

  .inner-header {
    background: #fff;

    .category-image {
      display: inline-block;
      img {
        height: 120px; } }

    .category-name {
      display: inline-block;
      position: relative;
      h1 {
        padding-left: 25px;
        font-family: "Arvo", serif;
        font-size: 48px;
        color: #3a3b3c;
        line-height: 120px; } } }

  .content-holder {
    display: block;
    position: relative;
    width: 98%;

    .course-filter-panel {
      width: 67%;
      display: block;
      position: relative;
      float: left;

      .filtering-area {
        display: block;
        border-bottom: 5px solid #ededed;
        padding-bottom: 15px;

        .top {
          display: block;
          width: 100%;
          height: 20px;
          background: #37474f;
          margin-bottom: 8px; }

        .bottom {
          display: block;
          width: 100%;
          background: #e98800;

