          .col-duration {
            display: inline-block;
            float: right;
            width: 17.4%;
            text-align: center;
            color: #6b6c6f;
            font-size: 20px;
            padding-top: 36px;
            span {
              font-size: 18px;
              color: #a6a8ab;
              font-family: "Roboto", sans-serif;
              line-height: 35px;
              font-weight: lighter; } } } } }

    .sliding-info-panel {
      display: block;
      position: relative;
      width: 33%;
      float: right;
      padding: 0 8px 25px 12px;

      .info-slider-holder {
        display: block;
        border: 1px solid #c2c2c2;
